<template lang="pug">
  include ./tools/mixins.pug
  +b.app(id="app")
    +e.sidebar
      P Avasystem
        button(style="display:block", id="btn-totalPoll", v-on:click="pollTotal()", :disabled="false") Общая статистика
    +e.main
      +e.FORM.poll
        +e.TABLE.table
          tr
            th 1
            th Уровень наличия рекламы
            th Сейчас
            th
            th Желательно
            th
          tr
            td A
            td Подбор релевантной рекламы на основе анализа поискоых запросов
            td
              output(id="valCurrentA1")
            td(rowspan="2")
              input(id="rangeCurrentAc1", type="range", orient="vertical")
            td
              output(id="valFutureA1") a1
            td(rowspan="2")
              input(id="rangeFutureAc1", type="range", orient="vertical")
          tr
            td C
            td Выбор рекламы осуществляеться случайным образом
            td
              output(id="valCurrentC1") c1
            td
              output(id="valFutureC1") c1
          tr
            td B
            td Реклама присутствует только не только результате выдачи поискового запроса
            td
              output(id="valCurrentB1") b1
            td(rowspan="2")
              input(id="rangeCurrentBd1", type="range", orient="vertical")
            td
              output(id="valFutureB1") b1
            td(rowspan="2")
              input(id="rangeFutureBd1", type="range", orient="vertical")
          tr
            td D
            td Реклама имеет ограниченное количество
            td
              output(id="valCurrentD1") d1
            td
              output(id="valFutureD1") d1
          tr
            th 2
            th Поиск по содержимому файлов
            th Сейчас
            th
            th Желательно
            th
          tr
          tr
            td A
            td Поиск по максимальному число форматов файлов
            td
              output(id="valCurrentA2") a2
            td(rowspan="2")
              input(id="rangeCurrentAc2", type="range", orient="vertical")
            td
              output(id="valFutureA2") a2
            td(rowspan="2")
              input(id="rangeFutureAc2", type="range", orient="vertical")
          tr
            td C
            td Поиск по несколько наиболее расспростаненным форматам файлов
            td
              output(id="valCurrentC2") c2
            td
              output(id="valFutureC2") c2
          tr
            td B
            td Пользователь может выбирать, какие форматы файлов должны учавствоваться в поиске.
            td
              output(id="valCurrentB2") b2
            td(rowspan="2")
              input(id="rangeCurrentBd2", type="range", orient="vertical")
            td
              output(id="valFutureB2") b2
            td(rowspan="2")
              input(id="rangeFutureBd2", type="range", orient="vertical")
          tr
            td D
            td Система сама выбирает формты файлов для поиска
            td
              output(id="valCurrentD2") d2
            td
              output(id="valFutureD2") d2
          tr
            th 3
            th Структура главной стрнаицы поисковой системы
            th Сейчас
            th
            th Желательно
            th
          tr
          tr
            td A
            td Минимализм - только строка поиска
            td
              output(id="valCurrentA3")
            td(rowspan="2")
              input(id="rangeCurrentAc3", type="range", orient="vertical")
            td
              output(id="valFutureA3")
            td(rowspan="2")
              input(id="rangeFutureAc3", type="range", orient="vertical")
          tr
            td C
            td На странице поиска находятся лишь блоки новостей и сервисов
            td
              output(id="valCurrentC3")
            td
              output(id="valFutureC3")
          tr
            td B
            td Страница поиска имеет блоки новостей, большое количество виджетов
            td
              output(id="valCurrentB3")
            td(rowspan="2")
              input(id="rangeCurrentBd3", type="range", orient="vertical")
            td
              output(id="valFutureB3")
            td(rowspan="2")
              input(id="rangeFutureBd3", type="range", orient="vertical")
          tr
            td D
            td Главная страница поиска напоминает небольшой новостной сайт
            td
              output(id="valCurrentD3")
            td
              output(id="valFutureD3")
          tr
            th 4
            th Графическое оформление
            th Сейчас
            th
            th Желательно
            th
          tr
          tr
            td A
            td Минимализм - оформление имеет простые решения, которые не отвлекаю при работе с сервисом
            td
              output(id="valCurrentA4")
            td(rowspan="2")
              input(id="rangeCurrentAc4", type="range", orient="vertical")
            td
              output(id="valFutureA4")
            td(rowspan="2")
              input(id="rangeFutureAc4", type="range", orient="vertical")
          tr
            td C
            td Среднестатистический уровень проработки оформления
            td
              output(id="valCurrentC4")
            td
              output(id="valFutureC4")
          tr
            td B
            td Для реализации интерфейса используются серьезные наработки в дизайне - каждая делать имеет значительную продуманность
            td
              output(id="valCurrentB4")
            td(rowspan="2")
              input(id="rangeCurrentBd4", type="range", orient="vertical")
            td
              output(id="valFutureB4")
            td(rowspan="2")
              input(id="rangeFutureBd4", type="range", orient="vertical")
          tr
            td D
            td Офорлмение - сильно не приоритетная задача поискового сервиса
            td
              output(id="valCurrentD4")
            td
              output(id="valFutureD4")
          tr
            th 5
            th Интеграция в мобильные устройства
            th Сейчас
            th
            th Желательно
            th
          tr
          tr
            td A
            td Интеграция в оболочку Android
            td
              output(id="valCurrentA5")
            td(rowspan="2")
              input(id="rangeCurrentAc5", type="range", orient="vertical")
            td
              output(id="valFutureA5")
            td(rowspan="2")
              input(id="rangeFutureAc5", type="range", orient="vertical")
          tr
            td C
            td Самостоятельные мобильные приложения
            td
              output(id="valCurrentC5")
            td
              output(id="valFutureC5")
          tr
            td B
            td Развитие голосового помощника
            td
              output(id="valCurrentB5")
            td(rowspan="2")
              input(id="rangeCurrentBd5", type="range", orient="vertical")
            td
              output(id="valFutureB5")
            td(rowspan="2")
              input(id="rangeFutureBd5", type="range", orient="vertical")
          tr
            td D
            td Минимальное наличие интеграции
            td
              output(id="valCurrentD5")
            td
              output(id="valFutureD5")
        button(class="btn-submit", id="btn-submit", type="submit", v-on:click="poll()", :disabled="false") Проголосовать
      +b.charts
        +e.current(id="chartCurrent")
        +e.totall(id="chartFuture")
</template>

<script>
var Highcharts = require('highcharts')
require('highcharts/modules/exporting')(Highcharts)
require('highcharts/highcharts-more.js')(Highcharts)

export default {
  name: 'app',
  methods: {
    pollTotal: function () {
      function randomInteger (min, max) {
        var rand = min + Math.random() * (max + 1 - min)
        rand = Math.floor(rand)
        return rand
      }

      var resNowAvr = []
      var resFutureAvr = []
      for (var i = 0; i < 4; i++) {
        resNowAvr.push(randomInteger(60, 80))
        resFutureAvr.push(randomInteger(60, 80))
      }

      Highcharts.chart('chartFuture', {
        chart: {
          polar: true,
          type: 'line'
        },

        title: {
          text: 'Общая статистика'
        },

        pane: {
          size: '80%'
        },

        xAxis: {
          categories: ['Google', 'Yandex', 'Mail', 'Yahoo'],
          tickmarkPlacement: 'on',
          lineWidth: 0,
          ceiling: 3
        },

        yAxis: {
          gridLineInterpolation: 'polygon',
          lineWidth: 0,
          min: 0
        },

        tooltip: {
          shared: true,
          pointFormat: '<span>{series.name}:</span> <b>{point.y:,.0f}</b><br/>'
        },

        legend: {
          align: 'right',
          verticalAlign: 'top',
          y: 70,
          layout: 'vertical'
        },

        series: [{
          name: 'Сейчас',
          data: resNowAvr,
          pointPlacement: 'on'
        }, {
          name: 'Желательно',
          data: resFutureAvr,
          pointPlacement: 'on'
        }]
      })

      document.getElementById('btn-totalPoll').setAttribute('disabled', 'true')
    },
    poll: function () {
      var valCurrentA1 = parseInt(document.getElementById('valCurrentA1').value)
      var valCurrentC1 = parseInt(document.getElementById('valCurrentC1').value)
      var valFutureA1 = parseInt(document.getElementById('valFutureA1').value)
      var valFutureC1 = parseInt(document.getElementById('valFutureC1').value)
      var valCurrentB1 = parseInt(document.getElementById('valCurrentB1').value)
      var valCurrentD1 = parseInt(document.getElementById('valCurrentD1').value)
      var valFutureB1 = parseInt(document.getElementById('valFutureB1').value)
      var valFutureD1 = parseInt(document.getElementById('valFutureD1').value)

      var valCurrentA2 = parseInt(document.getElementById('valCurrentA2').value)
      var valCurrentC2 = parseInt(document.getElementById('valCurrentC2').value)
      var valFutureA2 = parseInt(document.getElementById('valFutureA2').value)
      var valFutureC2 = parseInt(document.getElementById('valFutureC2').value)
      var valCurrentB2 = parseInt(document.getElementById('valCurrentB2').value)
      var valCurrentD2 = parseInt(document.getElementById('valCurrentD2').value)
      var valFutureB2 = parseInt(document.getElementById('valFutureB2').value)
      var valFutureD2 = parseInt(document.getElementById('valFutureD2').value)

      var valCurrentA3 = parseInt(document.getElementById('valCurrentA3').value)
      var valCurrentC3 = parseInt(document.getElementById('valCurrentC3').value)
      var valFutureA3 = parseInt(document.getElementById('valFutureA3').value)
      var valFutureC3 = parseInt(document.getElementById('valFutureC3').value)
      var valCurrentB3 = parseInt(document.getElementById('valCurrentB3').value)
      var valCurrentD3 = parseInt(document.getElementById('valCurrentD3').value)
      var valFutureB3 = parseInt(document.getElementById('valFutureB3').value)
      var valFutureD3 = parseInt(document.getElementById('valFutureD3').value)

      var valCurrentA4 = parseInt(document.getElementById('valCurrentA4').value)
      var valCurrentC4 = parseInt(document.getElementById('valCurrentC4').value)
      var valFutureA4 = parseInt(document.getElementById('valFutureA4').value)
      var valFutureC4 = parseInt(document.getElementById('valFutureC4').value)
      var valCurrentB4 = parseInt(document.getElementById('valCurrentB4').value)
      var valCurrentD4 = parseInt(document.getElementById('valCurrentD4').value)
      var valFutureB4 = parseInt(document.getElementById('valFutureB4').value)
      var valFutureD4 = parseInt(document.getElementById('valFutureD4').value)

      var valCurrentA5 = parseInt(document.getElementById('valCurrentA5').value)
      var valCurrentC5 = parseInt(document.getElementById('valCurrentC5').value)
      var valFutureA5 = parseInt(document.getElementById('valFutureA5').value)
      var valFutureC5 = parseInt(document.getElementById('valFutureC5').value)
      var valCurrentB5 = parseInt(document.getElementById('valCurrentB5').value)
      var valCurrentD5 = parseInt(document.getElementById('valCurrentD5').value)
      var valFutureB5 = parseInt(document.getElementById('valFutureB5').value)
      var valFutureD5 = parseInt(document.getElementById('valFutureD5').value)

      var currentA = Math.ceil((valCurrentA1 + valCurrentA2 + valCurrentA3 + valCurrentA4 + valCurrentA5) / 5)
      var futureA = Math.ceil((valFutureA1 + valFutureA2 + valFutureA3 + valFutureA4 + valFutureA5) / 5)
      var currentB = Math.ceil((valCurrentB1 + valCurrentB2 + valCurrentB3 + valCurrentB4 + valCurrentB5) / 5)
      var futureB = Math.ceil((valFutureB1 + valFutureB2 + valFutureB3 + valFutureB4 + valFutureB5) / 5)
      var currentC = Math.ceil((valCurrentC1 + valCurrentC2 + valCurrentC3 + valCurrentC4 + valCurrentC5) / 5)
      var futureC = Math.ceil((valFutureC1 + valFutureC2 + valFutureC3 + valFutureC4 + valFutureC5) / 5)
      var currentD = Math.ceil((valCurrentD1 + valCurrentD2 + valCurrentD3 + valCurrentD4 + valCurrentD5) / 5)
      var futureD = Math.ceil((valFutureD1 + valFutureD2 + valFutureD3 + valFutureD4 + valFutureD5) / 5)

      var resNow = [currentA, currentB, currentC, currentD]
      var resFuture = [futureA, futureB, futureC, futureD]
      Highcharts.chart('chartCurrent', {
        chart: {
          polar: true,
          type: 'line'
        },

        title: {
          text: 'Ваш результат'
        },

        pane: {
          size: '80%'
        },

        xAxis: {
          categories: ['Google', 'Yandex', 'Mail', 'Yahoo'],
          tickmarkPlacement: 'on',
          lineWidth: 0,
          ceiling: 3
        },

        yAxis: {
          gridLineInterpolation: 'polygon',
          lineWidth: 0,
          min: 0
        },

        tooltip: {
          shared: true,
          pointFormat: '<span>{series.name}:</span> <b>{point.y:,.0f}</b><br/>'
        },

        legend: {
          align: 'right',
          verticalAlign: 'top',
          y: 70,
          layout: 'vertical'
        },

        series: [{
          name: 'Сейчас',
          data: resNow,
          pointPlacement: 'on'
        }, {
          name: 'Желательно',
          data: resFuture,
          pointPlacement: 'on'
        }]
      })
      document.getElementById('btn-submit').setAttribute('disabled', 'true')
      document.querySelector('.app__table').setAttribute('style', 'opacity: 0.5')
    }
  }
}
</script>

<style lang="scss">
table tr:nth-child(6) th,
table tr:nth-child(12) th,
table tr:nth-child(18) th,
table tr:nth-child(24) th {
  padding-top: 35px;
}

table tr:nth-child(1) th:nth-child(3),
table tr:nth-child(1) th:nth-child(5),
table tr:nth-child(6) th:nth-child(3),
table tr:nth-child(6) th:nth-child(5),
table tr:nth-child(12) th:nth-child(3),
table tr:nth-child(12) th:nth-child(5),
table tr:nth-child(18) th:nth-child(3),
table tr:nth-child(18) th:nth-child(5),
table tr:nth-child(24) th:nth-child(3),
table tr:nth-child(24) th:nth-child(5) {
  width: 100px;
  text-align: center;
}

table tr:nth-of-type(even) td:nth-child(3),
table tr:nth-of-type(even) td:nth-child(5) {
  text-align: center;
}

table tr:nth-of-type(odd) td:nth-child(3),
table tr:nth-of-type(odd) td:nth-child(4) {
  text-align: center;
}

.highcharts-title {
  font-family: "Roboto", sans-serif;
}

.btn-submit {
  display: block;
  padding: 12px;
  margin: 15px auto;
  font-size: 14px;
  font-weight: 700;
  color: #477987;
  text-decoration: none;
  background-color: transparent;
  border: 1px solid rgba(99, 154, 169, 0.4);
  border-radius: 5px;

  &:hover {
    border-color: rgba(99, 154, 169, 0.8);
  }
}

button[type="submit"]:disabled {
  opacity: 0.5;
}

button[type="submit"]:disabled:hover {
  border-color: rgba(99, 154, 169, 0.4);
}

input[type="range"] {
  width: 8px;
  height: 80px;
  padding: 0 5px;
  appearance: slider-vertical;
}

.app {
  display: flex;
  min-height: 100vh;
  font-family: "Roboto", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  text-align: center;

  &__main {
    flex: 9;
  }

  &__poll {
    text-align: left;
  }

  &__table {
    box-sizing: border-box;
    display: table;
    width: 100%;
    padding: 20px 30px;
    margin: 0;
    table-layout: auto;
  }

  &__sidebar {
    display: flex;
    flex: 1;
    flex-basis: 100px;
    justify-content: center;
    min-width: 15%;
    background-color: #477987;

    p {
      position: fixed;
      color: #ffffff;

      button {
        font-size: 12px;
        line-height: 1.5;
        color: #ffffff;
        background: none;
        border: none;
        opacity: 0.7;

        &:hover {
          cursor: pointer;
          opacity: 1;
        }
      }
    }
  }
}
</style>



