<template lang="pug">
  include ../tools/mixins.pug
  +b.hello
    +e.H1.title {{ msg }}
    +e.H2.title-second Essential Links
    +e.UL.list
      +e.LI.list-item
        +e.A.link(href="https://vuejs.org" target="_blank") Core Docs
      +e.LI.list-item
        +e.A.link(href="https://forum.vuejs.org" target="_blank") Forum
      +e.LI.list-item
        +e.A.link(href="https://gitter.im/vuejs/vue" target="_blank") Gitter Chat
      +e.LI.list-item
        +e.A.link(href="https://twitter.com/vuejs" target="_blank") Twitter
      br
      +e.LI.list-item
        +e.A.link(href="http://vuejs-templates.github.io/webpack/" target="_blank") Docs for This Template
    +e.H2.title-second Ecosystem
    +e.UL.list
      +e.LI.list-item
        +e.A.link(href="http://router.vuejs.org/" target="_blank") vue-router
      +e.LI.list-item
        +e.A.link(href="http://vuex.vuejs.org/" target="_blank") vuex
      +e.LI.list-item
        +e.A.link(href="http://vue-loader.vuejs.org/" target="_blank") vue-loader
      +e.LI.list-item
        +e.A.link(href="https://github.com/vuejs/awesome-vue" target="_blank") awesome-vue
</template>
<script>
  export default {
    name: 'hello',
    data () {
      return {
        msg: 'Welcome to Your Vue.js App'
      }
    }
  }
</script>
<style lang="scss">
  .hello {
    &__title {
      font-weight: normal;
    }

    &__title-second {
      font-weight: normal;
    }

    &__list {
      padding: 0;
      list-style-type: none;
    }

    &__list-item {
      display: inline-block;
      margin: 0 10px;
    }

    &__link {
      color: #42b983;
    }
  }
</style>



