import 'normalize.css'
import Vue from 'vue'
import App from './App'
import router from './router'

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  template: '<App/>',
  components: { App },
  methods: {
    poll: function () {},
    pollTotal: function () {}
  }
})

var outputs = document.getElementsByTagName('output')
for (var i = 0; i < outputs.length; i++) {
  outputs[i].innerHTML = '50'
}

